section.login {

  form {
    border: 3px solid #f1f1f1; }

  input[type=text], input[type=password] {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    box-sizing: border-box; }

  button {
    background-color: #4CAF50;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 100%; }

  button:hover {
    opacity: 0.8; }


  button[disabled] {
    opacity: 0.5; }

  .imgcontainer {
    text-align: center;
    margin: 24px 0 12px 0; }

  img.avatar {
    width: 400px;
    border-radius: 50%; }

  .container {
    padding: 16px; } }

